@import "./../../Sass/variables.scss";
@import "./../../Sass/mixins.scss";

.watersport-center {
  display: grid;
  gap: 4rem;
  margin-top: 2rem;
}
.desc {
  line-height: 2;
  max-width: 45em;
}
.info {
  text-transform: capitalize;
  width: 300px;
  display: grid;
  grid-template-columns: 125px 1fr;
  span {
    font-weight: 700;
  }
}

@media (min-width: 992px) {
  .watersport-center {
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
}
